import React from "react";
import * as FormRouter from "./FormRouter";
import FormControl from "../../components/FormControl";

const OrderForm = ( { onSubmit }) => {

    const validate = (values) => {
        const errors = {};
        if (!values.deliveryAddress) {
          errors.deliveryAddress = " ";
        }
        if (!values.deliveryContact) {
          errors.deliveryContact = " ";
        }
        if (!/^\d{2,3}-\d{3,4}-\d{4}$/.test(values.deliveryContact)) {
          errors.deliveryContact =
            "  . (: 010-1234-5678)";
        }
        return errors;
      };

      
     return(
      <FormRouter.Form
      id="order-form"
      className = "OrderForm"
      validate={validate}
      onSubmit={onSubmit} 
      initialValues={{
        deliveryAddress: "",
        deliveryContact: "",
        paymentMethod: "",
        messageToShop: "",
        messageToRider: "",
      }}
      >
      <FormControl 
       label="" 
       required
       htmlFor="deliveryAddress"
       error={<FormRouter.ErrorMessage name="deliveryAddress"/>}
       >
      <FormRouter.Field 
       type="text"
       name="deliveryAddress"
       placeholder="  "
       autoFocus
      />
      </FormControl>
      <FormControl 
       label="" 
       required
       htmlFor="deliveryContact"
       error={<FormRouter.ErrorMessage name="deliveryContact"/>}
       >
     <FormRouter.Field 
      type="text"
      name="deliveryContact"
      placeholder=" "
     />
      </FormControl>
      <FormControl 
      label="" 
      required
      htmlFor="paymentMethod">
      <FormRouter.Field 
      as="select"
      name="paymentMethod"
      >
       <option value=""></option>
       <option value=""> </option>
      </FormRouter.Field>
      </FormControl>
      <FormControl 
      label=""
      htmlFor="messageToShop"
      >
      <FormRouter.Field as="textarea" name="messageToShop"/>
      </FormControl>
      <FormControl 
       label=""
       htmlFor="messageToRider">
      <FormRouter.Field as="textarea" name="messageToRider"/>
      </FormControl>
      </FormRouter.Form>

     )

}

export default OrderForm;