ursor: pointer;\n   text-transform: uppercase;\n\n }\n\n.drinkBtn .temBtn.active{\n\n   border:2px solid #f1658a;\n\n }\n\n\n /* cart */\n\n .cart {\n\n  width: 100%;\n  padding: 20px 0px;\n  margin:0 auto;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n\n }\n\n .cart ul{\n\n   padding: 0px;\n\n }\n\n\n .cart img{ height: 150px; text-align: center;}\n\n .cart .totalCount{\n\n   width: 100%;\n   padding: 20px 0px;\n   border:1px solid #ccc;\n\n }\n\n .cart .totalCount span{\n\n   display: block;\n   padding: 10px;\n   text-align: center;\n   font-size: 1.1em;\n   color:#555;\n\n }\n\n .cart .totalCount span strong{\n\n  font-weight: 700;\n }\n\n .cart .totalCount .option.hot strong{\n   \n   content : \"HOT\";\n }\n\n\n /* formContol */\n\n #order-form{\n\n  padding: 10px 10px 100px;\n  margin:0 auto;\n\n }\n .formControl {\n\n    max-width:1200px;\n    margin:0 auto;\n\n }\n\n .formControl label{\n\n   display:block;\n   padding: 10px 0px;\n   margin:0 auto;\n   color:#555;\n   font-size: 1em;\n   font-weight: 600;\n\n }\n\n .formControl label .required{\n\n   color:#de0000;\n   font-size: 0.8em;\n   \n }\n\n .formControl input , .formControl select{\n\n    width: 100%;\n    padding: 10px 5px;\n }\n\n .formControl textarea{\n\n    width: 100%;\n    padding: 30px 5px;\n\n }\n\n .formControl .error{ color:#de0000; font-size: 0.8em; padding: 5px 0px;}\n \n \n\n /* Card */\n\n .card {\n   width: 100%;\n   padding: 30px 0px 100px;\n   margin:0 auto;\n   display: flex;\n   flex-direction: column;\n   align-items: center;\n   justify-content: center;\n   \n }\n\n .card header{\n\n     padding: 30px 0px;\n     font-size: 1.3em;\n     color:#f1658a;\n     font-weight: 800;\n }\n\n .card main{\n\n     width: 100%;\n     padding: 20px 0px;\n     border:1px solid #ccc;\n     \n }\n\n .card main dl{\n\n   padding: 30px;\n   margin:5px;\n   display: flex;\n   border-bottom:1px solid #ccc\n\n }\n\n .card main dl:last-child{\n\n  b